/* 12 */
.btn-12 {
    position: relative;
    /* right: 20px; */
    bottom: 20px;
    border: none;
    background-color: none;
    box-shadow: none;
    width: 130px;
    height: 7vh;
    line-height: 42px;
    -webkit-perspective: 230px;
    perspective: 230px;

}

.btn-12 span {

    background: rgb(219, 230, 181);
    /* background: linear-gradient(0deg, rgb(162, 160, 93) 0%, rgb(155, 145, 67) 100%); */
    border-radius: 5px;
    box-shadow: 0 0 15px 20px rgba(203, 197, 137, 0.434);
    /* background: -webkit-linear-gradient(0deg, rgb(91, 78, 113) 0%, rgba(129, 5, 240, 1) 100%); */
    display: block;
    position: absolute;
    width: 130px;
    height: 40px;
    /* box-shadow: inset 2px 2px 2px 0px rgb(255, 250, 250),
    7px 7px 20px 0px rgba(0, 0, 0, .1),
    4px 4px 5px 0px rgba(0, 0, 0, .1); */
    border-radius: 5px;
    margin: 0;
    text-align: center;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all .3s;
    transition: all .3s;
}

.btn-12 span:nth-child(1) {
    box-shadow: -7px -7px 20px 0px rgb(252, 240, 240),
    -4px -4px 5px 0px rgb(230, 230, 230),
    7px 7px 20px 0px #0002,
    4px 4px 5px 0px rgba(92, 89, 89, 0.067);
    -webkit-transform: rotateX(90deg);
    -moz-transform: rotateX(90deg);
    transform: rotateX(90deg);
    -webkit-transform-origin: 50% 50% -20px;
    -moz-transform-origin: 50% 50% -20px;
    transform-origin: 50% 50% -20px;

}

.btn-12 span:nth-child(2) {
    -webkit-transform: rotateX(0deg);
    -moz-transform: rotateX(0deg);
    transform: rotateX(0deg);
    -webkit-transform-origin: 50% 50% -20px;
    -moz-transform-origin: 50% 50% -20px;
    transform-origin: 50% 50% -20px;
    font-family: 'Grek';
}

.btn-12:hover span:nth-child(1) {
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0),
    7px 7px 20px 0px rgba(0, 0, 0, 0),
    4px 4px 5px 0px rgba(0, 0, 0, 0);
    -webkit-transform: rotateX(0deg);
    -moz-transform: rotateX(0deg);
    transform: rotateX(0deg);
    font-family: 'Grek';
}

.btn-12:hover span:nth-child(2) {
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0),
    7px 7px 20px 0px rgba(0, 0, 0, 0),
    4px 4px 5px 0px rgba(0, 0, 0, 0);
    color: transparent;
    -webkit-transform: rotateX(-90deg);
    -moz-transform: rotateX(-90deg);
    transform: rotateX(-90deg);

}
