.player__right {
    grid-area: player__right;
    display: block;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    position: absolute;
    width: fit-content;
}

.player__progress {

    grid-area: player__progress;
    display: flex;
    position: absolute;
    height: 85%;
    width: 160%;
    right: -11;
    transform: rotateX(180deg);
    transition: 100ms;
    /* bottom:; */
}


