.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}


.modal.active {
    opacity: 0;
    /* transform: scale(1); */
    pointer-events: all;
    background-color: rgba(255, 255, 255, 0.9)
}

.modal__content {
    padding: 20px;
    border-radius: 12px;
    background-color: lightblue;
    height: 80%;
    max-height: 800px;
    width: 80%;
    max-width: 800px;
    transform: scale(1);
    transition: 0.4s all;
}


#lalala {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 30vh;

}

/* BUTTON */

.btn {
    border-radius: 4px;
    background-color: silver;
    border: none;
    color: #fff;
    text-align: center;
    font-size: 100%;
    padding: 1em;
    width: 90%;
    transition: all 0.5s;
    cursor: pointer;
    margin: .7em;
    box-shadow: 0 5px 5px -4px rgba(0, 0, 0, .7);
}

.btn {
    cursor: pointer;
    display: inline-block;
    /*position: relative;*/
    transition: 0.5s;
}

.btn:after {
    /*content: '»';*/
    position: absolute;
    opacity: 0;
    top: 1em;
    right: -1em;
    transition: 0.5s;
}

.btn:hover {
    background-color: sienna;
    padding-right: 1.5em;
    padding-left: .5em;
}

.btn:hover:after {
    opacity: 1;
    right: .8em;
}
